import { JewishCalendar } from "../JewishCalendar";
import { Parsha } from "../../util/Parsha/ParshaMap";
import { Temporal } from "temporal-polyfill";

/**
 * This class's main goal is to return the Weekly Haftorah reading said after the Weekly Parasha
 * reading. Which readings to say were taken from the Chumash "L'maan Shemo B'Ahavah" according to
 * the Sepharadic Minhag.
 * @see WeeklyParashaReadings
 */
export default class WeeklyHaftarahReading {

	/**
	 * This method returns a string that contains the weekly Haftorah. The {@link JewishCalendar}
	 * object passed into this method should be set to Saturday because the {@link JewishCalendar#getParshah()}
	 * method returns {@link com.kosherjava.zmanim.hebrewcalendar.JewishCalendar.Parsha#NONE} during
	 * the week.
	 * @param jCal the JewishCalendar object set to Saturday
	 * @return The haftorah for this week as a string
	 */
	public static getThisWeeksHaftarah(jCal: JewishCalendar): { text: string; source: string; } {
		const specialShabbatot = {
			[Parsha.SHKALIM]: {
				text: " ",
				source: '  "'
			},
			[Parsha.ZACHOR]: {
				text: " ",
				source: '  "'
			},
			[Parsha.PARA]: {
				text: " ",
				source: ' "'
			},
			[Parsha.HACHODESH]: {
				text: " ",
				source: ' "'
			},
			[Parsha.HAGADOL]: {
				text: "",
				source: ' '
			}
		}

		if (jCal.getSpecialShabbos() in specialShabbatot)
			// @ts-ignore
			return specialShabbatot[jCal.getSpecialShabbos()]

		// Rosh Hodesh & Erev Rosh Hodesh has its own Haftarot. Use that except if it's Matot Maseh, Re'eh or just Hanukah
		// Then default to weekly parasha.

		// The initial code wanted to make it more flexible and have each Parasha in its own ability to override ERH & RH,
		// but we refactored away from that since we know that only 2 Parashiot & 1 Event have the exception
		// Also, it combined Noah & Ki These, but we don't have that luxury because we're saving space on everything else.
		// (ironic it didn't combine Pekudeh & Wayaqhel-Pekudeh, among others)

		if (jCal.isYomTov()) {
			switch (jCal.getYomTovIndex()) {
				case JewishCalendar.ROSH_HASHANA:
					return { text: " ", source: "  "}
					// 2nd day: { text: " ", source: ' "'})
					// comment because Rosh Hashanah (20A)
					break;
				case JewishCalendar.YOM_KIPPUR:
					return { text: " ", source: ' "' };
					break;
				case JewishCalendar.SUCCOS:
					return { text: " ", source: ' "' };
					break;
					// 2nd day:   
					// comment because parallel to RH
				case JewishCalendar.CHOL_HAMOED_SUCCOS:
					return { text: " ", source: " "};
					break;
				case JewishCalendar.SHEMINI_ATZERES:
					return (jCal.getInIsrael() ? { text: " ", source: ' ' } : {text: " ", source: '  '});
					break;
				case JewishCalendar.PESACH:
					return {
						15: { text: " ", source: ' ' },
						16: { text: " ", source: '  "' },
						21: { text: " ", source: '  "' },
						22: { text: " ", source: ' ' }
					}[jCal.getJewishDayOfMonth()]!;
					break;
				case JewishCalendar.CHOL_HAMOED_PESACH:
					return { text: " ", source: ' "'}
					break;
				case JewishCalendar.SHAVUOS:
					if (jCal.getJewishDayOfMonth() == 7)
						return { text: "' ", source: " "};

					return { text: " ", source: " "};
			}
		}

		if (jCal.isChanukah())
			return ([7, 8].includes(jCal.getDayOfChanukah())
				? {
					text: " ",
					source: '  '
				} : {
					text: " ",
					source: ' '
				})

		if (![Parsha.MATOS_MASEI, Parsha.REEH].includes(jCal.getParshah())) {
			if (jCal.isErevRoshChodesh()) {
				return {
					text: " ",
					source: '  '
				};
			} else if (jCal.isRoshChodesh()) {
				return {
					text: " ",
					source: ' "'
				};
			}
		}

		const tammuz17 = jCal.clone()
		tammuz17.setJewishDate(tammuz17.getJewishYear(), JewishCalendar.TAMMUZ, 17)

		// @ts-ignore
		return {
			[Parsha.BERESHIS]: {
				text: " ",
				source: ' "'
			},
			[Parsha.NOACH]: {
				text: " ",
				source: ' "'
			},
			[Parsha.LECH_LECHA]: {
				text: " ",
				source: ' '
			},
			[Parsha.VAYERA]: {
				text: " ",
				source: '  '
			},
			[Parsha.CHAYEI_SARA]: {
				text: " ",
				source: '  '
			},
			[Parsha.TOLDOS]: {
				text: " ",
				source: ' '
			},
			[Parsha.VAYETZEI]: {
				text: " ",
				source: ' "'
			},
			[Parsha.VAYISHLACH]: {
				text: " ",
				source: ''
			},
			[Parsha.VAYESHEV]: {
				text: " ",
				source: ' '
			},
			[Parsha.MIKETZ]: {
				text: " ",
				source: '  '
			},
			[Parsha.VAYIGASH]: {
				text: " ",
				source: ' "'
			},
			[Parsha.VAYECHI]: {
				text: "",
				source: '  '
			},
			[Parsha.SHEMOS]: {
				text: " ",
				source: ' '
			},
			[Parsha.VAERA]: {
				text: " ",
				source: ' "'
			},
			[Parsha.BO]: {
				text: " ",
				source: ' "'
			},
			[Parsha.BESHALACH]: {
				text: " ",
				source: ' '
			},
			[Parsha.YISRO]: {
				text: " ",
				source: " "
			},
			[Parsha.MISHPATIM]: {
				text: " ",
				source: ' "'
			},
			[Parsha.TERUMAH]: {
				text: " ",
				source: "  "
			},
			[Parsha.TETZAVEH]: {
				text: "  ",
				source: ' "'
			},
			[Parsha.KI_SISA]: {
				text: ' ',
				source: '  "'
			},
			[Parsha.VAYAKHEL]: {
				text: " ",
				source: '  '
			},
			[Parsha.PEKUDEI]: {
				text: " ",
				source: '  '
			},
			[Parsha.VAYAKHEL_PEKUDEI]: {
				text: " ",
				source: '  '
			},
			[Parsha.VAYIKRA]: {
				text: " ",
				source: ' "'
			},
			[Parsha.TZAV]: {text: " ", source: " "},
			[Parsha.SHMINI]: {text: " ", source: "  "},
			[Parsha.TAZRIA]: {text: " ", source: "  "},
			[Parsha.METZORA]: {
				text: " ",
				source: '  '
			},
			[Parsha.TAZRIA_METZORA]: {
				text: " ",
				source: '  '
			},
			[Parsha.ACHREI_MOS]: {
				text: " ",
				source: ' "'
			},
			[Parsha.KEDOSHIM]: {
				text: " ",
				source: ' '
			},
			[Parsha.ACHREI_MOS_KEDOSHIM]: {
				text: " ",
				source: ' '
			},
			[Parsha.EMOR]: {
				text: "",
				source: ' "'
			},
			[Parsha.BEHAR]: {
				text: " ",
				source: ' "'
			},
			[Parsha.BECHUKOSAI]: {
				text: " ",
				source: ' "'
			},
			[Parsha.BEHAR_BECHUKOSAI]: {
				text: " ",
				source: ' "'
			},
			[Parsha.BAMIDBAR]: {
				text: " ",
				source: ' '
			},
			[Parsha.NASSO]: {
				text: " ",
				source: ' "'
			},
			[Parsha.BEHAALOSCHA]: {
				text: " ",
				source: ' '
			},
			[Parsha.SHLACH]: {
				text: "",
				source: ' '
			},
			[Parsha.KORACH]: {
				text: "",
				source: '  "'
			},
			[Parsha.CHUKAS]: {
				text: "",
				source: ' "'
			},
			[Parsha.BALAK]: {
				text: "",
				source: " "
			},
			[Parsha.CHUKAS_BALAK]: {
				text: "",
				source: " "
			},
			[Parsha.PINCHAS]:
				(Temporal.PlainDate.compare(jCal.getDate(), tammuz17.getDate()) == -1
					? {
						text: " ",
						source: ' "'
					} : {
						text: " ",
						source: " "
					}),
			[Parsha.MATOS]: {
				text: " ",
				source: " "
			},
			[Parsha.MASEI]: {
				text: " ",
				source: " "
			},
			[Parsha.MATOS_MASEI]: {
				text: " ",
				source: " "
			},
			[Parsha.DEVARIM]: {
				text: "",
				source: ' '
			},
			[Parsha.VAESCHANAN]: {
				text: "",
				source: ' '
			},
			[Parsha.EIKEV]: {
				text: " ",
				source: ' "'
			},
			[Parsha.REEH]: {
				text: " ",
				source: ' "'
			},
			[Parsha.SHOFTIM]: {
				text: " ",
				source: ' "'
			},
			[Parsha.KI_SEITZEI]: {
				text: " ",
				source: ' "'
			},
			[Parsha.KI_SAVO]: {
				text: " ",
				source: ' '
			},
			[Parsha.NITZAVIM]: {
				text: " ",
				source: ' "'
			},
			[Parsha.VAYEILECH]: {
				text: "",
				source: ' "'
			},
			[Parsha.NITZAVIM_VAYEILECH]: {
				text: " ",
				source: ' "'
			},
			[Parsha.HAAZINU]:
				(jCal.getJewishMonth() == JewishCalendar.TISHREI && jCal.getJewishDayOfMonth() > 10
					? { text: " ", source: '  "' }
					: { text: "", source: ' "' }),
			// Parsha.VZOS_HABERACHA is unused
		}[jCal.getParshah()]
	}
}
